
import React, { Component } from 'react'

export default class SingleTodo extends Component {

    removingFunction = (index)=>{
        this.props.remove(index)
    }

    render() {



    const todoItems = this.props.tasks.map((item,index)=>{
        return (
                <div className="ui segment flex" key={index}>
                    <p className='task'>{item}</p>
                    <i className="x icon pointer" onClick={()=>{this.removingFunction(index)}}></i>
                </div>
        );
    })

        return (
            <div>
                {todoItems}
            </div>
        )
    }
}
