import React, { Component } from 'react'
import SingleTodo from './SingleTodo';

export default class TodoLists extends Component {

    constructor(props){
        super(props)


        this.state={
            todosValue:'',
            todosArray:[]
        }
    }

     updateState = (event)=>{

        ;
        this.setState(()=>{
            return {
                todosValue:event.target.value,
            }
        });
        console.log(this.state.todosValue);
       
    }

    onSubmitHandler=(event)=>{
        event.preventDefault()
        this.setState((prevstate)=>{
            if(this.state.todosValue!==''){
                return {
                    todosArray:[...prevstate.todosArray, this.state.todosValue],
                    todosValue:''
                } 
            }
            
        })
        
    }

    deleteItemHandler=(id)=>{
        this.state.todosArray.splice(id, 1)
        this.setState((prevstate)=>{
             return {
                    todosArray:[...prevstate.todosArray],
                    todosValue:''
                } 
            
        })
    }

    render() {
        return (
            <article className="ui center aligned container">
                <h1 className='title'>Write Down Your Tasks</h1>
                <form className="ui center aligned container" onSubmit={this.onSubmitHandler}>
                    <div className="ui action input ">
                        <input type="text" className='input' placeholder="Add Todos..." value={this.state.todosValue} onChange={this.updateState} />
                        <input type='submit' className="ui button submit"/>
                    </div>
                </form>
                <div >
                    <SingleTodo tasks={this.state.todosArray} remove={this.deleteItemHandler}/>
                </div>
            </article>
        )
    }
}
